<template>
    <div>
        <p class="text-center font-bold font-times text-xl underline ">SOP</p>
        <div
          class="table-responsive-sm font-times p-3"
          style="box-shadow: 1px 4px 6px black; overflow: scroll"
          v-dragscroll
        >
          <!-- DEKSTOP -->
          <table
            class="table-print table-sm table-bordered table-striped table-hover table-print border-black"
            style="
            "
          >
            <thead class="mdb-color bg-black">
              <tr class="text-white">
                <th class="text-white fit text-uppercase text-lg" v-for="(item, index) in Object.keys(datanya[0])" :key="index+'th'" v-if="item!='id'">{{item}}</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in datanya" :key="index+'datanya'">
                <td class="text-lg fit">{{format(item.tanggal)}}</td>
                <td class="text-lg fit">
                    {{item.pemateri}}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
    </div>
</template>
<script>
import firebase from 'firebase'
let db = firebase.firestore()
var id = require("date-fns/locale/id");

export default {
    data(){
        return{
            datanya:[]
        }
    },
    methods: {
        format(tanggal) {
                return this.$datefns.format(
                new Date(tanggal),
                "DD MMMM YYYY",
                {
                    locale: id,
                }
                );
        },
        getData(){
            db.collection('tv-sop').onSnapshot(res=>{
                let data = res.docs.map(e=>{
                    return {
                        id:e.id,
                        ...e.data()
                    }
                })
                this.datanya = data
            })
        }
    },
    mounted() {
        this.getData()
    },
}
</script>
<style>
td.fit, 
th.fit {
    white-space: nowrap;
    width: 1%;
}
</style>